import React from "react";
import { useTheme } from "@mui/material/styles";
import { useMediaQuery } from "@mui/material";

import MenuDrawer from "./MenuDrawer";
import MenuFlat from "./MenuFlat";

export interface SectionType {
  id: string;
  text: string;
}
export interface MenuProps {
  sections: SectionType[];
}

export default function HeaderMenu() {
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.up("md"));

  const sections: SectionType[] = [
    { id: "section-about", text: " " },
    { id: "section-resume", text: "" },
    { id: "section-character", text: "" },
    { id: "section-experienced", text: " " },
    { id: "section-project", text: "" },
    { id: "section-why", text: "Q&A" },
    { id: "section-promising", text: " " },
    { id: "section-finish", text: "" },
  ];

  if (matches) {
    return <MenuFlat sections={sections} />;
  } else {
    return <MenuDrawer sections={sections} />;
  }
}

